<template>
  <view class="orders-container">
    <view class="header">
      <view class="back-btn" @click="goBack">
        <text class="back-icon">‚Üê</text>
        <text class="back-text">ËøîÂõû</text>
      </view>
      <view class="title">ÊàëÁöÑËÆ¢Âçï</view>
      <view class="refresh-btn" @click="loadOrders">
        <text class="refresh-icon">üîÑ</text>
        <text class="refresh-text">Âà∑Êñ∞</text>
      </view>
    </view>

    <view v-if="loading" class="loading">Âä†ËΩΩ‰∏≠...</view>
    <view v-else-if="orders.length === 0" class="empty">ÊöÇÊó†ËÆ¢Âçï</view>
    <view v-else class="order-list">
      <view v-for="order in orders" :key="order._id" class="order-item" @click="goDetail(order)">
        <view class="order-header">
          <view class="order-title">{{ getOrderTitle(order) }}</view>
          <view class="order-status" :class="{
            'status-pending': order.status === 'pending' || order.status === 'ÂæÖÁ°ÆËÆ§',
            'status-confirmed': order.status === 'confirmed' || order.status === 'Â∑≤Á°ÆËÆ§',
            'status-cancelled': order.status === 'cancelled' || order.status === 'Â∑≤ÂèñÊ∂à',
            'status-paid': order.status === 'paid' || order.status === 'Â∑≤ÊîØ‰ªò',
            'status-default': !order.status || (order.status !== 'pending' && order.status !== 'confirmed' && order.status !== 'cancelled' && order.status !== 'paid' && order.status !== 'ÂæÖÁ°ÆËÆ§' && order.status !== 'Â∑≤Á°ÆËÆ§' && order.status !== 'Â∑≤ÂèñÊ∂à' && order.status !== 'Â∑≤ÊîØ‰ªò')
          }">
            {{ getStatusText(order.status) }}
          </view>
        </view>
        <view class="order-content">
          <view class="order-info">
            <text class="info-label">Êä•Âêç‰∫∫Ôºö</text>
            <text class="info-value">{{ getApplicantsText(order) }}</text>
          </view>
          <view class="order-info">
            <text class="info-label">ÈáëÈ¢ùÔºö</text>
            <text class="info-value price">¬•{{ order.amount || order.totalFee || 0 }}</text>
          </view>
          <view class="order-info">
            <text class="info-label">ÊîØ‰ªòÊó∂Èó¥Ôºö</text>
            <text class="info-value">{{ formatDate(order.payTime) }}</text>
          </view>
        </view>
        <view class="order-footer">
          <text class="order-time">{{ formatDate(order.createTime) }}</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
// ÂØºÂÖ•‰∫ëÂØπË±°
const fahuiManagement = uniCloud.importObject('fahui-management')
const jointManagement = uniCloud.importObject('joint-management')

export default {
  data() {
    return {
      orders: [],
      loading: false
    }
  },
  onShow() {
    this.loadOrders()
  },
  methods: {
    goBack() {
      uni.navigateBack()
    },
    
    async loadOrders() {
      this.loading = true
      try {
        // Ëé∑Âèñ‰∏ìÂú∫Ê≥ï‰ºöËÆ¢Âçï
        const fahuiResult = await fahuiManagement.getOrders()
        let fahuiOrders = []
        if (fahuiResult.success) {
          fahuiOrders = fahuiResult.data || []
        }
        
        // Ëé∑ÂèñÂêàÂùõÊ≥ï‰ºöËÆ¢Âçï
        const jointResult = await jointManagement.getOrders()
        let jointOrders = []
        if (jointResult.success) {
          jointOrders = jointResult.data || []
        }
        
        // ÂêàÂπ∂ËÆ¢ÂçïÂπ∂ÊåâÊó∂Èó¥ÂÄíÂ∫èÊéíÂàó
        this.orders = [...fahuiOrders, ...jointOrders].sort((a, b) => {
          const timeA = new Date(a.createTime || a.create_time || 0)
          const timeB = new Date(b.createTime || b.create_time || 0)
          return timeB - timeA
        })
        
      } catch (error) {
        console.error('Âä†ËΩΩËÆ¢ÂçïÂ§±Ë¥•Ôºö', error)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },
    
    getOrderTitle(order) {
      if (order.fahuiType === 'joint') {
        return 'ÂêàÂùõÊ≥ï‰ºö'
      } else {
        return order.fahuiName || (order.fahuiProject && order.fahuiProject.name) || '‰∏ìÂú∫Ê≥ï‰ºö'
      }
    },
    
    getApplicantsText(order) {
      if (order.applicants && order.applicants.length > 0) {
        return order.applicants.map(a => a.name).join('„ÄÅ')
      } else if (order.participantName) {
        return order.participantName
      } else {
        return 'Êú™Áü•'
      }
    },
    
    getStatusText(status) {
      const statusMap = {
        'paid': 'Â∑≤ÊîØ‰ªò',
        'pending': 'ÂæÖÁ°ÆËÆ§',
        'confirmed': 'Â∑≤Á°ÆËÆ§',
        'cancelled': 'Â∑≤ÂèñÊ∂à',
        'ÂæÖÁ°ÆËÆ§': 'ÂæÖÁ°ÆËÆ§',
        'Â∑≤Á°ÆËÆ§': 'Â∑≤Á°ÆËÆ§',
        'Â∑≤ÂèñÊ∂à': 'Â∑≤ÂèñÊ∂à',
        'Â∑≤ÊîØ‰ªò': 'Â∑≤ÊîØ‰ªò',
        'ÂæÖÊîØ‰ªò': 'ÂæÖÊîØ‰ªò'
      }
      return statusMap[status] || status || 'Êú™Áü•'
    },
    
    getStatusClass(status) {
      switch (status) {
        case 'ÂæÖÁ°ÆËÆ§':
        case 'pending':
          return 'status-pending'
        case 'Â∑≤Á°ÆËÆ§':
        case 'confirmed':
          return 'status-confirmed'
        case 'Â∑≤ÂèñÊ∂à':
        case 'cancelled':
          return 'status-cancelled'
        case 'Â∑≤ÊîØ‰ªò':
        case 'paid':
          return 'status-paid'
        default:
          return 'status-default'
      }
    },
    
    formatDate(date) {
      if (!date) return '-'
      const d = new Date(date)
      return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')} ${String(d.getHours()).padStart(2, '0')}:${String(d.getMinutes()).padStart(2, '0')}`
    },
    
    goDetail(order) {
      uni.setStorageSync('orderDetail', order);
      uni.navigateTo({ url: '/pages/profile/orderDetail' });
    }
  }
}
</script>
